.cart-container {
    margin-top: 1em;
    background-color: white;
}

.cart-divider {
    border-top: 1px solid black;
    opacity: 0.3;
}

.cart-container table, th, td {
    border: 1px solid rgba(0, 0, 0, 0.3);
    border-collapse: collapse;
}

.cart-table {
    width: 100%;
    margin-top: 2em;
}

.cart-table-head {
    font-weight: bold;
}

.tc {
    align-items: center;
    padding: 1em;
    box-sizing: border-box;
}


.tc-left {
    text-align: left;
}

.tc-center {
    text-align: center;
}

.tc-right {
    text-align: right;
}

.cart-button-delete {
    color: black;
}

.cart-button-delete:hover {
    cursor: pointer;
    color: var(--main-bg-color)
}

.cart-price {
    font-weight: bold;
    color: var(--main-bg-color);
}

.cart-table-price-container {
    width: 100%;
    display: flex;
    flex-direction: row;
    margin-top: 1rem;

}

.cart-button-more {
    border: none;
    align-self: flex-start;
    color: white;
    background-color: var(--main-bg-color);
    padding: 1em;
    font-size: 1em;
    text-align: center;
    box-sizing: border-box;
}

.cart-button-more:hover {
    cursor: pointer;
    opacity: 0.8;
}

.btn-checkout {
    margin-top: 1em;
    width: 100%;
}

.btn-checkout:focus {
    outline: none;
}

.cart-button-add {
    border: none;
    padding: 0.5em;
    border: 1px solid #bdbdbd;
}

.cart-button-add:focus {
    outline: none;
}

.cart-input-amount {
    border: none;
    border-top: 1px solid #bdbdbd;
    border-bottom: 1px solid #bdbdbd;
    padding: 0.5em;
    box-sizing: border-box;
    width: 3em;
    text-align: center;
    width: 2em;
}

.cart-input-amount:focus {
    outline: none;
}

.cart-container-mobile {
    display: none;
}

.cart-element-mobile {
    display: flex;
}

.cart-element-right {
    width: 30%;
    min-width: 6em;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.card-main {
    opacity: 0.3
}

.delete-mobile {
    margin-bottom: 1em
}

.cart-button-more-mobile {
    text-align: center;
    margin-top: 1em;
    padding: 1em;
    font-size: 1em;
    background-color: var(--second-bg-color);
}

.cart-nav {
    background-color: white;
    padding: 0.5em;
    margin-top: 1em;
}

.cart-nav-link {
    color: var(--second-bg-color);
}

