    /*.cart-root-container {*/
    /*    width: 100%;*/
    /*}*/
}

@media screen and (max-width: 768px) {
    .cart-container-desktop {
        display: none;
    }

    .cart-container {
        padding: 0em;
    }

    .cart-container-mobile {
        display: block;
    }
}